* {
  transition: all .25s;
}

.loader {
  --duration: 2.7s;
  width: 44px;
  height: 44px;
  position: relative;
}

.loader:before {
  content: "";
  width: 6px;
  height: 6px;
  background: var(--accent-color);
  animation: dotRect var(--duration) cubic-bezier(.785, .135, .15, .86) infinite;
  z-index: 100;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: 37px;
  left: 19px;
  transform: translate(-18px, -18px);
}

.loader svg {
  width: 100%;
  height: 100%;
  display: block;
}

.loader svg rect, .loader svg polygon, .loader svg circle {
  fill: none;
  stroke: var(--background-contrast);
  stroke-width: 10px;
  stroke-linejoin: round;
  stroke-linecap: round;
}

.loader svg polygon {
  stroke-dasharray: 145 76 145 76;
  stroke-dashoffset: 0;
  animation: pathTriangle var(--duration) cubic-bezier(.785, .135, .15, .86) infinite;
}

.loader svg rect {
  stroke-dasharray: 192 64 192 64;
  stroke-dashoffset: 0;
  animation: pathRect var(--duration) cubic-bezier(.785, .135, .15, .86) infinite;
}

.loader svg circle {
  stroke-dasharray: 150 50 150 50;
  stroke-dashoffset: 75px;
  animation: pathCircle var(--duration) cubic-bezier(.785, .135, .15, .86) infinite;
}

.loader.triangle {
  width: 48px;
}

.loader.triangle:before {
  animation: dotTriangle var(--duration) cubic-bezier(.785, .135, .15, .86) infinite;
  left: 21px;
  transform: translate(-10px, -18px);
}

@keyframes pathTriangle {
  33% {
    stroke-dashoffset: 74px;
  }

  66% {
    stroke-dashoffset: 147px;
  }

  100% {
    stroke-dashoffset: 221px;
  }
}

@keyframes dotTriangle {
  33% {
    transform: translate(0);
  }

  66% {
    transform: translate(10px, -18px);
  }

  100% {
    transform: translate(-10px, -18px);
  }
}

@keyframes pathRect {
  25% {
    stroke-dashoffset: 64px;
  }

  50% {
    stroke-dashoffset: 128px;
  }

  75% {
    stroke-dashoffset: 192px;
  }

  100% {
    stroke-dashoffset: 256px;
  }
}

@keyframes dotRect {
  25% {
    transform: translate(0);
  }

  50% {
    transform: translate(18px, -18px);
  }

  75% {
    transform: translate(0, -36px);
  }

  100% {
    transform: translate(-18px, -18px);
  }
}

@keyframes pathCircle {
  25% {
    stroke-dashoffset: 125px;
  }

  50% {
    stroke-dashoffset: 175px;
  }

  75% {
    stroke-dashoffset: 225px;
  }

  100% {
    stroke-dashoffset: 275px;
  }
}

.loader {
  margin: 0 16px;
  display: inline-block;
}

html {
  -webkit-font-smoothing: antialiased;
}

*, * :before, * :after {
  box-sizing: border-box;
}

.loader_page {
  min-height: 100vh;
  min-width: 100vw;
  background: var(--background-color);
  animation: fadeOut 1.3s 2s;
  animation-iteration-count: initial;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.loader_page .dribbble {
  z-index: 100;
  display: block;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.loader_page .dribbble img {
  height: 28px;
  display: block;
}

.loadhide {
  transition: all .15s;
  display: none;
}

